import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { ProductsComponent } from './product-index/products.component';
// import { ProductDetailsComponent } from './product-details/product-details.component';
import { OnlyLoggedInUsers } from '../../guards/only-logged-in-users.guard';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: 'sellers', component: ProductsComponent },
    { path: 'sellers/pending', component: ProductsComponent },
    {
      path: 'sellers/add',
      loadChildren: () => import('./add-product/add-product.module').then(m => m.AddProductModule), canActivate: [OnlyLoggedInUsers]
    },
    {
      path: 'sellers/product-details/:id',
      loadChildren: () => import('./edit-product/edit-product.module').then(m => m.EditProductModule), canActivate: [OnlyLoggedInUsers]
    },
    { path: 'stores', component: ProductsComponent },
    { path: 'stores/pending', component: ProductsComponent },
    // { path: 'stores/product-details/:id', component: ProductDetailsComponent }
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProductsRoutingModule {
}
