import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ProductsServices } from '../../../services/products/products.services';
import { Router } from '@angular/router';
import { productColumn } from './product.column';
import { CategoriseServices } from '../../../services/categories/categories.services';
import { SellersServices } from '../../../services/sellers/sellers.services';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { BrandsServices } from '../../../services/brands/brands.services';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { saveAs } from 'file-saver';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
import { PoliciesServices } from '../../../services/policies/policies.services';
import { ShippingMethodsServices } from '../../../services/shipping-methods/shipping-methods.services';

@Component({
    selector: 'app-product',
    templateUrl: 'products.component.html',
    styleUrls: ['products.component.scss'],
})
export class ProductsComponent implements OnInit {
    @ViewChild('dialog') dialog: any;
    dataItem: GridDataResult = {
        data: [],
        total: 0
    };
    columnGrid: any = productColumn;
    autoCompleteData: any[] = [
        {
            name: 'category',
            list: []
        },
        {
            name: 'owner',
            list: []
        },
        {
            name: 'policy',
            list: []
        },
        {
            name: 'shipping_method',
            list: []
        },
        {
            name: 'brand',
            list: []
        },
    ];
    loading: boolean = false;
    showGrid: boolean = false;
    reviewed: number;
    lastMeta: any;
    currentDeleteProduct: any;
    currentActiveProduct: any;
    dialogActionType: string;
    dialogTitle: string;
    dialogMessage: string;
    type;
    type_id;


    constructor(
        private productService: ProductsServices,
        private categoryService: CategoriseServices,
        private sellersService: SellersServices,
        private policiesService: PoliciesServices,
        private shippingMethodsService: ShippingMethodsServices,
        private brandService: BrandsServices,
        private convertFilter: FilterToQueryService,
        private toaster: NbToastrService,
        private router: Router,
        private dialogService: NbDialogService,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.type = this.router.url.split('/')[2];
        this.type_id = (this.type == 'stores') ? 1 : 2;
        this.reviewed = (this.router.url.split('/')[3] == 'pending') ? 2 : 1;
        this.columnGrid.filter(ele => ele.field === 'command')[0].command.filter(ele => ele.action === 'active')[0].hidden = (this.reviewed == 1) ? true : false;
        this.getProducts();
    }

    getProducts(meta?: any) {
        meta = meta ? (meta + '&status=' + this.reviewed) : ('status=' + this.reviewed);
        meta = meta + '&type=' + this.type_id;
        this.lastMeta = meta;
        this.loading = true;
        this.productService.getProducts(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res['total'];
            },
            errpr => {
                this.loading = false;
            },
            () => {
                this.loading = false;
            }
        );
    }

    deleteProduct(dialogRef: any) {
        this.productService.deleteProduct(this.currentDeleteProduct.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', { status: 'success' });
            },
            error => {
                this.toaster.show(error.error.message, 'Error', { status: 'danger' });
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getProducts(this.lastMeta);
                this.currentDeleteProduct = null;
            },
        );
    }


    activeProduct(dialogRef: any) {
        this.productService.approvePendingProduct(this.currentActiveProduct.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', { status: 'success' });
            },
            error => {
                this.toaster.show(error.error.message, 'Error', { status: 'danger' });
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getProducts(this.lastMeta);
                this.currentActiveProduct = null;
            },
        );
    }


    getCategories(meta: string) {
        this.categoryService.getParentCategorise(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'category')[0].list = res['data'];
            }, error => {
            });
    }

    getSellers(meta: string) {
        this.sellersService.getSellers(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'owner')[0].list = res['data'];
            }, error => {
            });
    }

    getPolicies(meta: string) {
        this.policiesService.getPolicies(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'policy')[0].list = res['data'];
            }, error => {
            });
    }

    getShippingMethods(meta: string) {
        this.shippingMethodsService.getShippingMethods(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'shipping_method')[0].list = res['data'];
            }, error => {
            });
    }

    getBrands(meta: string) {
        this.brandService.getBrands(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'brand')[0].list = res['data'];
            },
            error => {
            },
            () => {
            },
        );
    }

    filterChange(event: any) {
        this.getProducts(this.convertFilter.convertToQuery(event));
    }

    autoCompleteFilterChangeCheck(event: any) {
        const type = event.fieldName;
        if (type === 'category') {
            const meta = 'name=' + event.value;
            this.getCategories(meta);
        }

        if (type === 'policy') {
            const meta = 'name=' + event.value;
            this.getPolicies(meta);
        }

        if (type === 'shipping_method') {
            const meta = 'name=' + event.value;
            this.getShippingMethods(meta);
        }

        if (type === 'owner') {
            const meta = 'name=' + event.value + '&activation=true';
            this.getSellers(meta);
        }

        if (type === 'brand') {
            const meta = 'name=' + event.value;
            this.getBrands(meta);
        }
    }

    commandActionClick(event: any) {
        console.log('./products/' + this.type + '/product-details/' + event.data.id);
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./products/' + this.type + '/product-details/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteProduct = event.data;
                this.open(this.dialog, event.action.action);
                break;

            case 'active':
                this.currentActiveProduct = event.data;
                this.open(this.dialog, event.action.action);
                break;
        }
    }

    open(dialog: TemplateRef<any>, action: string) {
        if (action === 'delete') {
            this.dialogActionType = 'delete';
            this.dialogTitle = 'Delete Confirm';
            this.dialogMessage = 'Are you sure to delete product <strong>' + this.currentDeleteProduct.name + '</strong> ?';
            this.dialogService.open(dialog);
        }

        if (action === 'active') {
            this.dialogActionType = 'active';
            this.dialogTitle = 'Active Confirm';
            this.dialogMessage = 'Are you sure to active product <strong>' + this.currentActiveProduct.name + '</strong> ?';
            this.dialogService.open(dialog);
        }
    }

    dialogAction(dialogRef: any, action: string) {
        if (action === 'delete') {
            this.deleteProduct(dialogRef);
        }

        if (action === 'active') {
            this.activeProduct(dialogRef);
        }

    }


    changeTap(event: any) {
        this.showGrid = false;
        if (event.tabTitle === 'Non Reviewed') {
            // this.reviewed = 2;
            // let filter: string = 'reviewed=false';
            this.columnGrid.filter(ele => ele.field === 'command')[0].command.filter(ele => ele.action === 'active')[0].hidden = false;
            this.getProducts();
            this.showTable();
        }

        if (event.tabTitle === 'Non Completed') {
            // this.reviewed = 3;
            // let filter: string = 'reviewed=false';
            this.columnGrid.filter(ele => ele.field === 'command')[0].command.filter(ele => ele.action === 'active')[0].hidden = true;
            this.getProducts();
            this.showTable();
        }

        if (event.tabTitle === 'Active') {
            // this.reviewed = 1;
            // let filter: string = 'reviewed=true';
            this.columnGrid.filter(ele => ele.field === 'command')[0].command.filter(ele => ele.action === 'active')[0].hidden = true;
            this.getProducts();
            this.showTable();
        }
    }

    showTable() {
        setTimeout(() => {
            this.showGrid = true;
        }, 200);
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'products/export/excel?' + this.lastMeta : 'products/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'products.xlsx');
        }),
            error => this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
    }
}
