import { Component, Input, OnInit } from '@angular/core';
import { AbstractControl, AbstractControlDirective } from '@angular/forms';

@Component({
  selector: 'error-message',
  templateUrl: './error-message.component.html',
  styleUrls: ['./error-message.component.scss']
})
export class ErrorMessageComponent implements OnInit {
  @Input() controlName: AbstractControl | AbstractControlDirective;
  @Input() name: string;
  public errorMsgList: any = [];
  errorMessage = {
    'required': (params: any) => this.name + ` is required`,
    'maxlength': (params: any) => this.name + ` maximum ${params.requiredLength} characters are allowed`,
    'minlength': (params: any) => this.name + ` minimum ${params.requiredLength} characters are required`,
    'pattern': (params: any) => this.name + ` invalid format`,
    'min': (params: any) => this.name + ` minimum amount shouldn't be less than ${params.min}`,
    'max': (params: any) => this.name + ` maximum amount shouldn't be less than ${params.max}`,
    'whitespace': (params: any) => this.name + ` white spaces are not allowed`,
  };

  constructor() { }

  ngOnInit(): void {
  }

  public listErrors() {
    if (!this.controlName) return [];
    if (this.controlName.errors) {
      this.errorMsgList = [];
      Object.keys(this.controlName.errors).map(error => {
        this.controlName.touched || this.controlName.dirty ?
          this.errorMsgList.push(this.errorMessage[error](this.controlName.errors[error])) : '';
      });
      return this.errorMsgList;
    }
    else {
      return [];
    }
  }

}
